<template>
  <div class="pt-5 px-2" style="min-height: 500px" :dir="exam.lang == 2 ? 'ltr' : 'rtl'">
    <loading v-if="loading" />

    <div v-if="examNotStarted" class="startQuiz center">
      <button
        @click="
          () => {
            startExamHandler();
          }
        "
        class="btn btn-primary"
      >
        ابدأ الأمتحان
      </button>
    </div>

    <!----------------------------------------------->
    <!----------------------------------------------->
    <div v-if="studentResultReady" class="container p-1">
      <exam-result :examResult="result" />
    </div>
    <!----------------------------------------------->
    <!----------------------------------------------->

    <div v-if="examStarted" class="py-4 px-2 bg-light">
      <div class="timer-cont">
        <div
          v-if="examStarted"
          class="timer sticky-top"
          :class="timerAlert ? 'timerAlert' : 'timerRegular'"
        >
          <h2 v-if="remainingTime > 0">
            {{ timer }}
          </h2>
          <h2 v-if="remainingTime == 0">انتهى الوقت</h2>
        </div>
      </div>
      <!---------------------------->

      <section
        v-if="examStarted"
        class="exam"
        :class="{ alert_border: remainingTime == 0, alert_border: timerAlert }"
      >
        <hr />
        <div class="text-center">
          <h3>({{ exam.name }})</h3>
          <h4>{{ exam.stage | stage }}</h4>
          <h3 v-if="exam.unit">({{ exam.unit | section(exam.stage) }})</h3>
          <h4 v-if="exam.year">
            <Strong>({{ exam.year + "/" + (exam.year + 1) }})</Strong> العام الدراسي
          </h4>
          <hr />
          <div v-if="!exam.isTask">
            <p>اخر ميعاد للأمتحان</p>
            <strong>{{ exam.deadLine | moment("dddd, MMMM Do YYYY") }}</strong>
          </div>
        </div>
        <hr />

        <div
          id="top"
          v-for="(section, s) in solutionModel.sections"
          :key="s"
          class="questions-section px-3"
        >
          <div
            class="question-ttle-image"
            :class="{ ltr: exam.lang == '2', rtl: exam.lang != '2' }"
          ></div>
          <v-alert border="right" color="blue-grey" dark>
            <display-question
              :type="section.titleType"
              :src="section.text"
              :lang="exam.lang"
            />
          </v-alert>
          <hr />
          <v-card>
            <v-card flat v-for="(question, q) in section.questions" :key="q">
              <v-card-text>
                <div
                  class="question _question"
                  :class="{ ltr: exam.lang == '2', rtl: exam.lang != '2' }"
                >
                  <div class="question-text">
                    <display-question
                      :type="question.contentType"
                      :src="question.question"
                      :lang="exam.lang"
                      :index="q + 1"
                    />
                  </div>
                  <!-------------------ANSWER FOR EXAM ------------------------------->
                  <!-------------------ANSWER FOR EXAM ------------------------------->
                  <div class="answer">
                    <v-textarea
                      v-if="section.questionsType == 0"
                      v-model="question.userAnswer"
                      label="The Answer"
                      auto-grow
                      outlined
                      rows="3"
                      row-height="25"
                      shaped
                    ></v-textarea>
                    <v-radio-group
                      v-if="section.questionsType == 1"
                      v-model="question.userAnswer"
                      @change="
                        () =>
                          (question.degree =
                            question.userAnswer == question.modelAnswer
                              ? question.fullDegree
                              : 0)
                      "
                    >
                      <v-radio
                        v-for="(choice, c) in question.choices"
                        @click="saveProgress"
                        :key="c"
                        :label="choice"
                        :value="choice"
                      ></v-radio>
                    </v-radio-group>
                  </div>
                </div>
              </v-card-text>
            </v-card>
          </v-card>
          <!------------------------------------------>
        </div>
        <hr />
        <div v-if="submitting" class="center">
          <loading />
        </div>
        <div class="row justify-content-center p-2">
          <div v-if="examStarted" class="col-md-4">
            <v-btn @click="submitAnswers" depressed color="primary"> Finish </v-btn>
          </div>
        </div>
        <v-divider />
      </section>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import ExamResult from "./result.vue";
import { generateExamModel } from "../../functions";
import { eventBus } from "../../main";
import PdfViewer from "../../components/pdf-viewer.vue";
import { delay } from "../../functions";

export default {
  components: {
    ExamResult,
    PdfViewer,
  },
  props: ["exam"],
  created() {
    console.log(this.exam);
    this.solutionModel = { ...this.exam };
    this.tab = this.solutionModel.sections.map((sm) => null);
  },
  data() {
    return {
      tab: [null],
      timer: "00:00",
      timerAlert: false,
      startTime: 0,
      remainingTime: 0,
      startExam: false,
      loading: false,
      showExamDoneAlert: false,
      submitting: false,
      solutionModel: null,
      result: null,
      counter: null,
      correction: null,
    };
  },
  computed: {
    examReady() {
      return !!this.exam;
    },
    examNotStarted() {
      return this.examReady && !this.startExam;
    },
    examStarted() {
      return this.examReady && this.startExam && !this.result;
    },
    sentToCorrect() {
      return this.examReady && this.showExamDoneAlert;
    },
    studentResultReady() {
      const is = Object.keys(this.exam).length > 0 && this.result;
      return is;
    },
  },
  // computed: {
  //   examm() {
  //     console.log(this.exam);
  //     return "jhjhjh";
  //   },
  // },
  methods: {
    startExamHandler() {
      this.setTimer();
      this.startExam = true;
    },
    ////////////////////////////////////////////////////
    setTimer() {
      const examTimerName = `timer-${this.exam._id}`;
      if (!localStorage.getItem(examTimerName)) {
        localStorage.setItem(examTimerName, new Date().getTime());
        this.startTime = new Date().getTime();
      } else {
        this.startTime = +localStorage.getItem(examTimerName);
      }
      this.remainingTime =
        this.exam.timer * 60000 + this.startTime - new Date().getTime();
      if (this.remainingTime < 0) this.remainingTime = 300;
      /////////////////////////////////////////////////
      let remainingPerSecond = this.remainingTime;
      this.counter = setInterval(() => {
        remainingPerSecond = remainingPerSecond - 1000;
        let timer = new Date(remainingPerSecond).toString();
        timer = timer.split("Thu Jan 01 1970 ")[1].split(" GMT")[0];
        timer =
          +timer.split(":")[0] -
          2 +
          ":" +
          timer.split(":")[1] +
          ":" +
          timer.split(":")[2];
        this.timer = timer;
        if (remainingPerSecond < 60000) {
          this.timerAlert = !this.timerAlert;
        }
        if (remainingPerSecond < 2000) {
          clearInterval(this.counter);
          this.remainingTime = 0;
          this.timerAlert = true;
          if (this.$store.getters.adminData.autoFinishExam) {
            this.submitAnswers();
          }
        }
      }, 1000);
    },
    //////////////////////////////////////////////////////
    submitAnswers() {
      let completed = true;
      this.solutionModel.sections.forEach((sec) => {
        sec.questions.map((ques) => {
          if (ques.userAnswer === "") completed = false;
        });
      });
      if (!completed) {
        document.getElementById("top").scrollIntoView({ behavior: "smooth" });
        return this.$store.dispatch("writemessage", "Complete your answers...");
      }

      this.sendAnswers();
    },

    ////////////////////////////////////////////////////////////////////////////////
    sendAnswers() {
      if (this.examDone) return;
      // if (!submitted && this.isTimerBad()) return;
      this.correctExam();
      // return;
      delay(1000);
      this.submitting = true;
      axios.post(`sendUnitExamSolutionByStudent`, this.correction).then((res) => {
        this.submitting = false;
        if (res.data.err) return this.$store.dispatch("writemessage", res.data.err);
        if (res.data.solution) {
          this.result = res.data.solution;
        } else return alert("لم يتم حفظ النتيجة");

        this.$store.dispatch("writemessage", res.data.message);
        this.examDone = true;
        const examTimerName = `timer-${this.exam._id}`;
        localStorage.removeItem(examTimerName);
        localStorage.removeItem("answer");
        clearInterval(this.counter);
        this.remainingTime = 0;
      });
    },

    ///////////////////////////////////////////////////////////////////////
    ///////////////////////////////////////////////////////////////////////
    correctExam() {
      const correction = { ...this.solutionModel };
      correction["examId"] = this.exam._id;
      correction["userId"] = this.$store.getters.userId;
      correction["username"] = this.$store.getters.username;
      correction["phone"] = this.$store.getters.phone;
      const remain = this.remainingTime > 0 ? true : false;
      correction["inTime"] = remain;
      correction["done"] = true;
      /////////////////////////////////
      if (!this.exam.autoCorrect) {
        correction["done"] = false;
        this.correction = correction;
      }
      ////////////////////////////////////////////////////
      ////////////////////////////////////////////////////

      let totalDegree = 0;
      const sections = this.solutionModel.sections.map((sec) => {
        return {
          ...sec,
          questions: sec.questions.map((ques) => {
            let degree = 0;
            if (ques.userAnswer == ques.modelAnswer) {
              totalDegree += ques.fullDegree;
              degree = ques.fullDegree;
            }
            return { ...ques, degree };
          }),
        };
      });

      correction["totalDegree"] = totalDegree;
      correction["sections"] = sections;
      this.correction = correction;
    },
    saveProgress() {
      localStorage.setItem("answer", this.solutionModel);
    },
  },
};
</script>

