  <div id="result">
    <div v-if="result" class="examResults bg-light">
      <!---------------dddddddd-------------->
      <!---------------dddddddd-------------->
      <!---------------dddddddd-------------->

      <div v-if="sentToCorrect">
        <div class="center cover_cont bg-dark">
          <h1 class="text-center underline">تم ارسال الامتحان بنجاح</h1>
        </div>
      </div>
      <!---------------dddddddd-------------->
      <!---------------dddddddd-------------->
      <!---------------dddddddd-------------->
      <fireworks v-if="hooray" />
      <!-- <audio ref="hooray">
        <source :src="audio" type="audio/mp3" />
      </audio>-->
      <div v-if="popup_text" class="mess">{{ popup_text }}</div>
      <br />
      <!----------------------------->

      <div class="text-center">
        <h3>({{ result.name }})</h3>
        <h4>{{ result.stage | stage }}</h4>
        <h3 v-if="result.unit">({{ result.unit | section(result.stage) }})</h3>
        <h4 v-if="result.year">
          <Strong>({{ result.year + "/" + (result.year + 1) }})</Strong> العام الدراسي
        </h4>
