        </div>
      </div>
      <div class="question-header">
        <h2 style="color: rgb(196, 26, 26); font-weight: bolder">
          ({{ mark }} / {{ fullMark }})
        </h2>
      </div>
      <!------------------------------->

      <div
        v-for="(section, s) in result.sections"
        :key="s"
        class="questions-section"
        :class="{ ltr: result.lang == '2', rtl: result.lang != '2' }"
      >
        <div
          class="question-title-image"
          :class="{ ltr: result.lang == '2', rtl: result.lang != '2' }"
        >
          <display-question
            :src="section.text"
            :type="section.titleType"
            :lang="result.lang"
          />
        </div>
        <div
          v-for="(question, q) in section.questions"
          :key="q"
          class="exam-question _question"
          :class="{ ltr: result.lang == '2', rtl: result.lang != '2' }"
          :dir="result.lang == '2' ? 'ltr' : 'rtl'"
        >
          <div class="answer">
            <div class="top">
              <display-question
                :src="question.question"
                :type="question.contentType"
                :lang="result.lang"
                :index="q + 1"
              />
              <hr />
            </div>
            <!--------------------------------------->
            <div class="bottom">
              <div class="answer" style="min-width: 30%">
                <div v-if="section.questionsType == 0" class="text">
                  <div
                    :class="{
                      correct: question.modelAnswer == question.userAnswer,
                      wrong: question.modelAnswer != question.userAnswer,
                    }"
                  >
                    <h5>{{ question.userAnswer }}</h5>
                  </div>
                  <br />
                  <div v-if="question.modelAnswer != question.userAnswer" class="correct">
                    <h5>{{ question.modelAnswer }}</h5>
                  </div>
                </div>

                <v-radio-group
                  v-if="question.choices.length > 0"
                  v-model="question.userAnswer"
                >
                  <v-radio
                    width="50%"
                    class="choice"
                    :class="getOptionClass(choice, question)"
                    v-for="(choice, c) in question.choices"
                    readonly
                    :key="c"
                    :label="choice"
                    :value="choice"
                  ></v-radio>
                </v-radio-group>
              </div>
              <p />
              <hr />
              <div v-if="question.typicalAnswer != ''">
                <label>Answer: </label>
                <v-alert outlined type="success" text>{{
                  question.typicalAnswer
                }}</v-alert>
              </div>
            </div>
            <!--------------------------------------->
            <div class="degree">
              <span>({{ question.degree }}/{{ question.fullDegree }})</span>
              <span
                :style="[
                  question.degree < question.fullDegree
                    ? { color: 'red' }
                    : { color: 'green' },
                ]"
                style=""
                :class="
                  question.degree < question.fullDegree
                    ? 'fa fa-times times'
                    : 'fa fa-check check'
                "
                aria-hidden="true"
              ></span>
            </div>
            <!--------------------------------------->
          </div>
          <!--  -->
        </div>
      </div>
      <!--------------------------------------->
      <hr />
    </div>
  </div>
