<script>
import Fireworks from "../../components/fireworks";
import Axios from "axios";
export default {
  props: ["examResult"],
  components: {
    Fireworks,
  },
  async mounted() {
    this.result = this.examResult;
    if (!this.result.done) return this.sentToCorrect;
    this.initiate();
    document.getElementById("result").scrollIntoView({ behavior: "smooth" });
    // if (this.$route.path.includes("userTaskSolution")) {
    //   const userId = this.$route.params.userId;
    //   const lessonId = this.$route.params.lessonId;
    //   const res = await Axios.post("/getUserTaskSolution", { userId, lessonId });
    //   this.result = res.data.solution;
    // }
  },
  watch: {
    result(val) {
      this.initiate();
    },
  },
  data() {
    return {
      hooray: false,
      audio: "",
      popup_text: "",
      result: null,
      sentToCorrect: false,
    };
  },
  computed: {
    fullMark() {
      let mark = 0;
      this.result.sections.forEach((sec) => {
        sec.questions.forEach((ques) => {
          mark = mark + ques.fullDegree;
        });
      });
      return mark;
    },
    mark() {
      let mark = 0;
      this.result.sections.forEach((sec) => {
        sec.questions.forEach((ques) => {
          mark = mark + +ques.degree;
        });
      });
      return mark;
    },

    adminData() {
      return this.$store.getters.adminData;
    },
  },
  methods: {
    initiate() {
      if (this.result.done) {
        const hooray = this.$refs.hooray;
        if (this.result.totalDegree === this.result.fullDegree) {
          this.hooray = true;
          this.popup_text = "ممتاز يابني والله استمر";
          // hooray.play();
        } else if (this.result.totalDegree / this.result.fullDegree >= 0.8) {
          this.popup_text = "كويس .. شوية كمان بقى";
          // hooray.play();
        } else if (this.result.totalDegree / this.result.fullDegree >= 0.6) {
          this.popup_text = "لا شد حيلك معايا شوية";
        } else if (this.result.totalDegree / this.result.fullDegree <= 0.5) {
          this.popup_text = "لا ده انتا عايزلك قعدة بقى";
        }
        setTimeout(() => {
          this.hooray = false;
          this.popup_text = null;
        }, 5000);
      }
    },
    getOptionClass(choice, question) {
      if (choice == question.userAnswer && question.modelAnswer == question.userAnswer)
        return "correct";
      else if (
        choice == question.userAnswer &&
        question.modelAnswer != question.userAnswer
      )
        return "wrong";
      else if (choice == question.modelAnswer) return "correct";
    },
  },
};
</script>

